<template>
  <img
    class="magic-ui-img magic-ui-image-module"
    :style="{ opacity: opacity, filter: `blur(${blur}px) hue-rotate(${hueRotate}deg)` }"
    :src="config.src"
    alt="magic ui image"
  />
</template>
<script lang="ts">
import { computed, defineComponent, PropType, ref, watch } from 'vue';

import { MImg } from '../../types';
import useApp from '../../useApp';

export default defineComponent({
  props: {
    config: {
      type: Object as PropType<MImg>,
      default: () => ({
        opacity: 0,
        blur: 0,
        hueRotate: 0,
      }),
    },

    model: {
      type: Object,
      default: () => ({}),
    },
  },
  setup(props) {
    useApp(props);
    const opacity = ref(100);
    watch(
      () => props.config.opacity,
      (newValue) => {
        opacity.value = newValue / 100;
      },
      {
        immediate: true,
      },
    );
    return {
      opacity,
      hueRotate: computed(() => props.config.hueRotate),
      blur: computed(() => props.config.blur),
      // clickHandler() {
      //   if (props.config.url) window.location.href = props.config.url;
      // },
    };
  },
});
</script>
